"use client";

import { useState, useEffect } from "react";
import { createPortal } from "react-dom";

export default function AddMenuModal({ onClose }) {
  const [menuName, setMenuName] = useState("");
  const [submenuName, setSubmenuName] = useState("");
  const [mounted, setMounted] = useState(false);

  useEffect(() => setMounted(true), []);
  if (!mounted) return null;

  const handleSubmit = () => {
    console.log("Tambah Menu/Submenu:", { menuName, submenuName });
    onClose();
  };

  return createPortal(
    <div className="fixed inset-0 bg-black/40 flex justify-center items-center z-[9999]">
      <div className="bg-white p-6 rounded-lg w-96 shadow-xl">
        <h3 className="text-lg font-semibold mb-3">Tambah Menu / Submenu</h3>

        <label className="block mb-2 text-sm text-gray-700">Nama Menu</label>
        <input
          value={menuName}
          onChange={(e) => setMenuName(e.target.value)}
          className="w-full border p-2 rounded mb-4 focus:ring-2 focus:ring-blue-400 focus:outline-none"
          placeholder="Contoh: Employee Management"
        />

        <label className="block mb-2 text-sm text-gray-700">
          Nama Submenu (opsional)
        </label>
        <input
          value={submenuName}
          onChange={(e) => setSubmenuName(e.target.value)}
          className="w-full border p-2 rounded mb-4 focus:ring-2 focus:ring-blue-400 focus:outline-none"
          placeholder="Contoh: Employee List"
        />

        <div className="flex justify-end space-x-2">
          <button
            onClick={onClose}
            className="px-4 py-1.5 rounded border border-gray-300 hover:bg-gray-100 text-sm font-medium"
          >
            Batal
          </button>
          <button
            onClick={handleSubmit}
            className="px-4 py-1.5 rounded bg-blue-600 text-white hover:bg-blue-700 text-sm font-medium"
          >
            Simpan
          </button>
        </div>
      </div>
    </div>,
    document.body
  );
}
